body{
    background-color: #040425;
    color: #fff;
}
nav{
    background-color: #070727;
}
a{
    color: #ffffff !important;
}
.navbar-toggler .bar{
    display: block;
    background-color: #fff;
    width: 25px;
    height: 3px;
    margin: 5px auto;
    transition: all 0.3s ease-in-out;
}

.bn31 {
    background-image: linear-gradient(to right, #006175 0%, #ec5527 100%);
    border-radius: 40px;
    box-sizing: border-box;
    color: #ffffff;
    display: block;
    height: 50px;
    font-size: 1.4em;
    padding: 4px;
    position: relative;
    text-decoration: none;
    width: 7em;
    z-index: 2;
  }
  
  .bn31:hover {
    color: #fff;
    text-decoration: none;
  }

  
  .bn31 .bn31span {
    align-items: center;
    background: #070727;
    border-radius: 40px;
    display: flex;
    justify-content: center;
    height: 100%;
    transition: background 0.5s ease;
    width: 100%;
  }
  
  .bn31:hover .bn31span {
    background: transparent;
  }
  .container1 * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
  .container1 {
    min-height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 100px 0;
  }
  .timeline {
    width: 80%;
    height: auto;
    max-width: 800px;
    margin: 0 auto;
    position: relative;
  }
  
  .timeline> ul {
    list-style: none;
  }
  .timeline ul li {
    padding: 20px;
    background-color: #1e1f22;
    color: white;
    border-radius: 10px;
    margin-bottom: 20px;
  }
  .timeline ul li:last-child {
    margin-bottom: 0;
  }
  .timeline-content h1 {
    font-weight: 500;
    font-size: 25px;
    line-height: 30px;
    margin-bottom: 10px;
  }
  .timeline-content p {
    font-size: 16px;
    line-height: 30px;
    font-weight: 300;
  }
  .timeline-content .date {
    font-size: 12px;
    font-weight: 300;
    margin-bottom: 10px;
    letter-spacing: 2px;
  }
.road-lists{
    list-style-type:disc ;
}
@media only screen and (min-width: 768px) {
.timeline:before {
    content: "";
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 2px;
    height: 100%;
    background-color: gray;
}
.timeline >ul> li {
    width: 50%;
    position: relative;
    margin-bottom: 50px;
}
.timeline >ul> li:nth-child(odd) {
    float: left;
    clear: right;
    transform: translateX(-30px);
    border-radius: 20px 0px 20px 20px;
}
.timeline >ul >li:nth-child(even) {
    float: right;
    clear: left;
    transform: translateX(30px);
    border-radius: 0px 20px 20px 20px;
}
.timeline >ul >li::before {
    content: "";
    position: absolute;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color: gray;
    top: 0px;
}
.timeline >ul >li:nth-child(odd)::before {
    transform: translate(50%, -50%);
    right: -30px;
}
.timeline >ul >li:nth-child(even)::before {
    transform: translate(-50%, -50%);
    left: -30px;
}
.timeline-content .date {
    position: absolute;
    top: -30px;
}
.timeline >ul >li:hover::before {
    background-color: aqua;
}
}
@media screen and (max-width:968px){
    nav{
        display: relative;
    }
    .nav-container{
        position: absolute;
        display:grid;
        grid-template-columns: auto;
        margin:2px auto;
        justify-content: center;
        width: 100%;
        top:75px;
        right: 0;
        opacity: 0;
        transition: all 0.5s ease;
    }
    .nav-btn, .nav-item1{
        margin-bottom: 5px;
        text-align: center;
        display: table;
    }
    .nav-container.active{
        background-color: #070727;
        opacity: 1;
        right: 0;
        transition: all 0.5s ease;
    }
    .menu-toggle .bar{
        display: block;
    }
    #mobile-menu.is-active .bar:nth-child(2){
        opacity: 0;
    }
    #mobile-menu.is-active .bar:nth-child(1){
        transform: translateY(8px) rotate(45deg);
    }
    #mobile-menu.is-active .bar:nth-child(3){
        transform: translateY(-8px) rotate(-45deg);
    }
    .step-inner{
        font-weight: 200;
    }
}
.typed-cursor{
    display: none;
}
.mt-100{
    margin-top: 100px;
}
p{
    font-size: 1.3em;
}
/* buttons */
.btn-list{
    list-style-type: none;
}
.btn-icon{
    display: inline-block;
    padding: auto;
    font-size: 1.5em;
    color: #040425;
    border-radius: 50%;
    width: 39px;
    height: 39px;
    text-align: center;
    padding-top: 1.7px;
    border: #fff 1px solid;
}
.btn-icon:hover{
    background-color: #fff;
    color: #070727 !important;
}
.wrap{
    word-wrap: break-word;
}